nav {
  margin-bottom: 20px;
}
.input {
  max-width: 1000px;
  width: 40vw;
}
.search-bar {
  position: absolute;
  left: 20%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.nav-div {
  position: absolute;
  bottom: -20px;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  background: rgb(206, 206, 206);
  font-size: 14px;
  font-weight: bolder;
}
.nav-categories {
  margin: 0 3vw;
  color: rgb(27, 27, 27);
}
.categories-container {
  margin: 0 auto;
}
.footer-icon{
  font-size: 15px;
}
strong.name{
  font-size:12px;
}
.dev-bottom-row{
  position:relative;
  left:30px;
  bottom:20px;
}
.card-container {
  font-size: 11px;
  line-height: 15px;
  margin: 5px;
  border-radius: 10px; /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    rgba(243, 248, 253, 1) 0%,
    rgba(250, 252, 254, 0) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(243, 248, 253, 1)),
    to(rgba(250, 252, 254, 0))
  );
  background: -o-linear-gradient(
    top,
    rgba(243, 248, 253, 1) 0%,
    rgba(250, 252, 254, 0) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(243, 248, 253, 1) 0%,
    rgba(250, 252, 254, 0) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f3f8fd', endColorstr='#00fafcfe',GradientType=0 ); /* IE6-9 */
}
.social-title{
  position:relative;
  left:15px;
  font-family:'Courier New', Courier, monospace;
  font-weight:bold;
  font-size:30px;
}
.social-links{
  text-align:center;
  position:absolute;
  right:-80px;
  top: 20px;
}
.social-icons {
 font-size:60px;
 padding: 0 0 0 30px;
}
section.foot {
  position:relative;
  margin-top: 30px;
  padding-top:20px;
  background: rgb(199, 199, 199);
}
div.tile-box {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 50px;
  font-size: 26px;
  background-color: rgba(0, 0, 0, 0.52);
  color: white;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.tile {
  position: relative;
}
.medium-tiles{
  margin-top:30px;
}
p.description {
  display:flow-root;
  font-size: 15px;
  font-weight: normal;
  margin-left: -10px;
  overflow:hidden;
  opacity: 0;
  max-height:0;
  -webkit-transition: max-height 0.1s ease-out;
  -o-transition: max-height 0.1s ease-out;
  transition: max-height 0.1s ease-out;
}
article:hover{
  cursor:pointer;
}
p.description.show{
  overflow:hidden;
  max-height:200px;
  opacity:1;
  -webkit-transition: max-height 0.3s ease-in;
  -o-transition: max-height 0.3s ease-in;
  transition: max-height 0.3s ease-in;
}

h1 { 
    display: block;
    font-size: 2em;
    margin-top: 0.67em;
    margin-bottom: 0.67em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
.carousel { 
    width: 50%;
    height: auto;
}
#artwork-panel {
  margin: 0 px;
  padding: 30 px;
}
#portfolio-artwork {
  /* height: 400px; */
  width: auto;
}

/* css for artist panel */
#artistPanelContainer {
  width: 85%;
  height: 40%;
}
#artistPanelBox {
  height: 45px!important;
}
#artistPanelTitle {
  font-size: 4em;
  height: 6vw;
}
#artistPanelSubtitle {
  font-size: 1.1em;
  /* height: 4vw; */
}
#artistPanelImage {
  margin-top: 75px;
  
}
#artwork-card {
  width: 30%;
  margin: 15px;
}
#edit-artwork-card {
  font-size: 4em;
}
#edit-portfolio-artwork {
font-size: 4em;
}


